.header {
    grid-area: h;
    display: grid;
    grid-template-columns: 50px 1fr 1fr;
    margin: auto 0;
}

.img {
    width: 50px;
}

.headerText {
    align-self: center;
    padding: 0 10px;
}

.loginBlock {
    justify-self: end;
    align-self: center;
    margin: 0 20px;
}

.a__userLogin {
    text-decoration: none;
    margin: 0 20px
}